root,
body,
.App {
    margin: 0;
    padding: 0;
    min-height: 100vh;
}

* {
    font-family: "Roboto", sans-serif;
    box-sizing: border-box;
}

.App {
    display: flex;
}

.controller__container {
    flex: 3;
    padding: 15px 20px;
    background: #636e72;

    & > * {
        margin: 20px 0;
    }

    .logo {
        text-align: center;
    }

    .btn {
        padding: 20px 0;
        display: grid;
        border-top: 1px solid #dfe6e9;
        border-bottom: 1px solid #dfe6e9;
        grid-gap: 10px;
        grid-template-columns: repeat(auto-fill, minmax(30%, 1fr));
        grid-template-rows: repeat(2, minmax(34px, 1fr));

        a {
            text-decoration: none;
            text-align: center;
            line-height: 34px;
        }

        & > * {
            outline: none;
            border: none;

            border-radius: 3px;
            font-weight: bold;
            background: #0984e3;
            color: #fefefe;
            padding: 10px;
            font-size: 14px;
        }

        button:active {
            filter: brightness(1.2);
        }

        .form__checkbox {
            display: flex;
            justify-content: center;

            input[type="checkbox"] {
                display: none;

                & + label {
                    display: flex;
                    justify-content: center;

                    align-items: center;
                }

                & + label:before {
                    content: "";
                    display: block;
                    height: 16px;
                    width: 16px;
                    margin-right: 5px;
                    background: #fefefe;
                }

                &:checked + label:before {
                    content: "\2714";
                    text-align: center;
                    line-height: 17px;
                    color: black;
                }
            }
        }

        .form__input {
            outline: none;
            border: none;
            display: block;
            border-radius: 3px;
            font-weight: bold;
            grid-column-start: 2;
            grid-column-end: 4;
            padding: 10px;
            text-align: center;
            font-size: 14px;

            &::placeholder {
                opacity: 0.6;
                color: #fefefe;
            }
        }
    }

    .color__input {
        .color__col {
            display: flex;
            margin-bottom: 10px;
            & > * {
                border: none;
                outline: none;
                display: block;
                font-size: 14px;
                height: 34px;
                border-radius: 3px;
            }

            input {
                background: #dfe6e9;
                margin-right: 20px;
                text-indent: 10px;
                flex: 1;
            }

            button {
                color: #fefefe;
                background: #ff7675;
                font-weight: bold;
                width: 34px;
                transition: 100ms ease-in-out;

                &:active {
                    transform: scale(1.1);
                }
            }
        }
    }
}

.colors__container {
    flex: 7;
    background: #dfe6e9;
    display: grid;

    padding: 20px;
    grid-gap: 20px;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    grid-template-rows: repeat(auto-fill, minmax(200px, 1fr));

    .colors__card {
        text-align: center;
        border-radius: 3px;
        display: flex;

        justify-content: center;
        align-items: center;
        font-size: 16px;
        transition: 0.1s ease-in-out;
        font-weight: bold;
        p {
            padding: 20px;
            display: block;
        }

        &:active {
            transform: scale(1.1);
        }
    }
}

@media screen and (max-width: 768px) {
    .controller__container {
        display: none;
    }
}
